*{
  margin:0;
  padding: 0;
  box-sizing: border-box;
}

body{
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.4;
}

a{
  color: #333;
  text-decoration: none;
}

.container{
  padding: 0 1rem;
}

.btn{
  display: inline-block;
  border: none;
  background: orange;
  color: #fff;
  padding: 7px 20px;
  cursor: pointer;
}

.btn:hover{
  background: rgb(255, 187, 0);;
}

input[type = text]{
  border:1px solid #f4f4f4;
  padding: 7px 20px;
}